import { useEffect, useState } from "react";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import { compose } from "redux";
import { Header, Segment, Input, Icon } from "semantic-ui-react";
import { searchUser } from "../../api/user";
import NavigationActions from "../../store/navigation/actions";

const { fetching, success, error } = NavigationActions;

const MessagesHeader = (props) => {
  const { token } = props;
  const [userSearch, setUserSearch] = useState("");
  const [userData, setUserData] = useState({});

  const handleSearchUser = async () => {
    await searchUser(token, userSearch).then((res) => {
      if (res.success) {
        setUserData(res.data);
      }
    });
  };

  useEffect(() => {
    if (userSearch) {
      handleSearchUser();
    }
    // eslint-disable-next-line
  }, [userSearch]);

  return (
    <Segment clearing>
      {/* <Header fluid="true" as="h2" floated="left" styled={{ marginBottom: 0 }}>
        <span>
          {channelName}
          {!isPrivateChannel && (
            <Icon
              name={isChannelStarred ? "star" : "star outline"}
              size="small"
              color={isChannelStarred ? "yellow" : ""}
              onClick={handleStar}
            />
          )}
        </span>
        <Header.Subheader>{numUniqueUsers}</Header.Subheader>
      </Header> */}

      <Header floated="left">
        <Input
          size="mini"
          icon="search"
          name="searchTerm"
          placeholder="Search Users..."
          onChange={(e) => setUserSearch(e.target.value)}
          //   loading={searchLoading}
        />
      </Header>

      <Header floated="right">
        <Input
          size="mini"
          icon="search"
          name="searchTerm"
          placeholder="Search Messages..."
          //   onChange={handleSearchChange}
          //   loading={searchLoading}
        />
      </Header>
    </Segment>
  );
};

const mapStateToProps = (state) => {
  return {
    token: state.Auth?.accessToken,
    isFetching: state.navigation?.isFetching,
  };
};

export default compose(
  withRouter,
  connect(mapStateToProps, { fetching, success, error })
)(MessagesHeader);
